        <Article :title=title :desc=desc :date=date>
            <p>Like everything a computer also can be splited in components in order to decrease his complexity and help us to understand it. Some authors split the computer in 3 main components: user application, operation system and hardware.</p>
            <p>In this article I will summarize each one of this components in order to show the reader a big picture about the computer working.</p>
            <h2>User Applications</h2>
            <p>It is used by the final user. An economist organizing his job in a spreadsheet editor, a civil engineer designing a building project using a computer-aided-design (CAD), or a programmer writing code in a text editor. The computer exist for it,
                help people and companies through user applications. The applications are build using a computer language, it is a manner of teach the computer do a task.</p>
            <h2>Operation System</h2>
            <p>When the programmer create a program, it does not do everything alone, it will interact with the operational system (OS), but what would be this interaction?. During this interaction the OS will help the program to do a task, offering some generic
                functionalities, as: write data at an output device, read data from an input device, allocate memory, manage the portion of memory that each program are using to avoid conflicts.</p>
            <h3>System Calls</h3>
            <div class="image">
                <b-img src="syscall.jpeg" fluid alt="Responsive image"></b-img>
            </div>
            <p>The program interact with the operational system by system calls, when it happen the program stop and the OS begin to run.</p>
            <p>The program can interact directly with the OS or through a default library, in Linux systems there is a library called glib (GNU library C).</p>
            <h3>The Kernel</h3>
            <p>The OS main component is called Kernel, it contains all system call interfaces and is his responsibility manage drives and the memory. In the figure above was used a figure that show one Linux architecture, but the concept of Kernel is present
                in all operational systems.</p>
            <h2>Hardware</h2>
            <p>All components described above only can be visualized although abstractions and diagrams, but the hardware is something physic, it can be seen and touched. It is responsible for execute the instructions created by a programmer although
                a language program.</p>
            <p>The computer hardware is compounded for many components, the main are: input and output devices, processor and memory. The hardware performs some basic function in all computers, as read and write data, processing and storage.</p>
            <p>The interactions between them happens as follow: the processor fetch data and instructions from memory, the input devices write data in memory and the output devices read data from memory and show the computation result to user although a monitor or
                send it to other computer although a network board.</p>
            <p>The computer science curses and system analysis curse does not go deeper in hardware, the limit studied is logic gates.</p>
            <p class="quote">“Now, every one of these hardware devices is constructed from an integrated package of elementary logic gates. And these gates, in turn, can be built from primitive gates like Nand and Nor. Of course every one of these gates consists of several
                switching devices, typically implemented by transistors. And each transistor is made of—Well, we won’t go further than that, because that’s where computer science ends and physics starts.”</p>
            <p>The phrase above is in the book Building a Computer from First Principle. It means that hardware details are not in computer science’s scope, so we do not need to know how some hardware’s tasks are executed, we can abstract it and study only what
                it do.</p>
