<template>
   
        <Page>
            <div>
                <h2 style="margin-top: 10px">Articles</h2>
                <div style="margin-bottom:10px">2019</div>
                <li v-for='article in articles2019' v-bind:key='article.title'>
                    <v-link href="/computer_layers">{{article.title}}</v-link>
                    <br>
                    <div style="font-style:italic">
                        {{article.desc}}
                    </div>
                </li>
            </div>
        </Page>
    
</template>

<script>
import Page from '../components/Page'
import VLink from '../components/VLink'
export default {
    name: 'app',
    components: {
        Page,
        VLink
    },
    data() {
        return {
            articles2019: []
        }
    },
    methods: {
        setArticles() {
            this.articles2019.push(this.createArticle('A Brief Explanation About The Main Components of a Computer', 'This article describe the main parts of a computer and theirs responsabilities.', '07/Ago/2019'));
        },
        createArticle(title, desc, date) {
            let article = {
                title: title,
                desc: desc,
                date: date
            }
            return article
        }
    },
    created() {
        this.setArticles();
    }
}
</script>

<style scoped>
a{
    color:cornflowerblue
}
</style>
